.contact_main {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 80vh;
  margin: auto;
  font-family: Work Sans;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.contact_form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 45%;
}
.contact_form_header {
  margin-bottom: 15px;
  padding-left: 10px;
  width: 80%;
}
.contact_form_header h4 {
  color: #159eec;
  font-size: 18px;
  font-family: Work Sans;
  font-weight: 700;
  margin-bottom: 15px;
}
.contact_form_header h3 {
  color: #0079a1;
  font-size: 32px;
  font-family: Tilda Sans VF;
  font-weight: 700;
}
.contact_form_input {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  background-color: #0079a1;
  border-radius: 15px;
}
.contact_form_input input {
  font-size: 16px;
  width: 50%;
  padding: 15px 10px;
  border: 1px solid white;
  color: white;
}

.contact_form_input input::placeholder {
  color: white;
}
.contact_form_input textarea::placeholder {
  color: white;
}
.contact_form_input textarea {
  font-size: 16px;
  width: 100%;
  height: 200px;
  padding: 15px 10px;
  border: 1px solid white;
  border-top: none;
  color: white;
  border-bottom: none;
  color: white;
}
.contact_form_input button {
  width: 100%;
  font-size: 16px;
  color: #0079a1;
  border: 1px solid white;
  border-top: none;
  border-bottom: none;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 15px;
  background-color: #d1edff;
}
#inputBig {
  width: 100%;
  border-top: none;
}
.contact_info {
  width: 50%;
  display: flex;
}
.contact_info_ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 450px;
}
.contact_info_ul li {
  width: 48%;
  color: #0079a1;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #d1edff;
  padding: 30px 25px;
  margin: 20px 0;
  margin-bottom: 0;
  border-radius: 5px;
}
.svg {
  stroke: #0079a1;
}
.contact_info_ul li span {
  font-size: 18px;
  font-weight: 700;
}

.contact_info_ul li:hover {
  cursor: pointer;
  background-color: #0079a1;
  color: white;
  .svg {
    stroke: white;
  }
}

@media all and (max-width: 1500px) {
  .contact_main {
    height: 100vh;
  }
}
@media all and (max-width: 1300px) {
  .contact_main {
    width: 80%;
  }
}
@media all and (max-width: 1200px) {
  .contact_form_header h3 {
    font-size: 25px;
  }
  .contact_info_ul li {
    justify-content: space-around;
  }
}
@media all and (max-width: 1100px) {
  .contact_main {
    width: 90%;
  }
  .contact_form_header h3 {
    font-size: 20px;
  }
  .contact_info_ul li span {
    font-size: 15px;
  }
}
@media all and (max-width: 1000px) {
  .contact_main {
    height: 100%;
  }
  .contact_form {
    width: 80%;
    margin-top: 50px;
  }
  .contact_info {
    width: 80%;
  }
  .contact_info_ul {
    justify-content: space-around;
  }
}
@media all and (max-width: 900px) {
  .contact_main {
    width: 100%;
  }
}
@media all and (max-width: 600px) {
  .contact_form {
    width: 90%;
    margin-top: 50px;
  }
  .contact_info {
    width: 90%;
  }
}
@media all and (max-width: 500px) {
  .contact_info_ul li {
    margin: 15px 0;
    padding: 15px;
  }
  .contact_form_input textarea {
    height: 150px;
  }
}
@media all and (max-width: 400px) {
  .contact_info_ul li {
    margin: 5px 0;
    padding: 5px;
    justify-content: center;
  }
  .contact_info_ul {
    height: 350px;
  }
}
